:root {
 color-scheme: light dark;
 --site-margin: 75px;
 --content-row-padding: 100px;
}
@media (prefers-color-scheme: dark) {
 :root {
 color: white;
 background-color: --var(text-background-color);
 }
}
@media (max-width: 1000px) and (min-width: 751px) {
 :root {
 --site-margin: 40px;
 --content-row-padding: 60px;
 }
}
@media (max-width: 750px) {
 :root {
 --site-margin: 25px;
 --content-row-padding: 50px;
 }
}

body {
 font-family: system-ui, -apple-system, BlinkMacSystemFont, Helvetica, Arial, sans-serif;
 font-weight: 300;
 font-size: 1.05rem;
 margin: 0;
 display: flex;
 flex-direction: column;
 min-height: 100vh;
}

main {
 flex-grow: 1;
}

a {
 color: var(--link-color);
 text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
 margin-top: 0;
}

h1 {
 line-height: 1;
 font-size: 2.4rem;
 font-weight: 500;
 letter-spacing: -0.015em;
}
h1.hero {
 font-size: 3rem;
}

h2 {
 font-size: 1.8rem;
 font-weight: 500;
 margin-bottom: 0.5rem;
}

h3 {
 margin-top: 0;
 margin-bottom: 0.5rem;
 font-weight: 500;
}

hr {
 border: 0;
 border-top: 1px solid var(--separator-color);
}

hr.narrow {
 width: 175px;
 height: 3px;
 border: none;
 background-color: var(--separator-color);
 border-radius: 2px;
}

code {
 font-family: ui-monospace, monospace;
 padding: 0 5px;
 border-radius: 5px;
 background-color: rgba(0, 0, 0, 0.01);
 border: 1px solid var(--grid-color);
}
@media (prefers-color-scheme: dark) {
 code {
 background-color: rgba(255, 255, 255, 0.01);
 }
}

pre code {
 display: block;
 white-space: pre;
 -webkit-overflow-scrolling: touch;
 overflow-x: scroll;
 font-size: 0.75em;
 width: calc(100% - 20px);
 padding: 5px 10px;
}

@media (prefers-color-scheme: dark) {
 img.logo {
 filter: invert(1);
 }
}

input {
 font-size: 1rem;
}
@media (prefers-color-scheme: dark) {
 input {
 color: white;
 }
}

input:focus {
 outline: none;
}

input[type=email] {
 background: none;
 border: none;
 border-radius: 0;
 border-bottom: 1px solid gray;
 padding: 13px 0;
}

.button {
 -webkit-appearance: none;
 appearance: none;
 background-color: var(--control-accent-color);
 border: none;
 border-radius: 10px;
 padding: 14px 24px;
 color: white;
 cursor: pointer;
 font-size: 1em;
 border: 1px solid transparent;
}
.button.button-secondary {
 color: inherit;
 background-color: inherit;
 border: 1px solid var(--grid-color);
}
.button:disabled {
 background-color: var(--grid-color);
 cursor: inherit;
}

.page-content {
 max-width: 800px;
 padding: var(--site-margin);
 margin: 0 auto;
}
.page-content.main-page {
 text-align: center;
}
.page-content img, .page-content video {
 max-width: 100%;
 border-radius: 10px;
}

.narrow-width {
 max-width: 600px;
 margin-left: auto;
 margin-right: auto;
}

.site-width {
 max-width: 1200px;
 padding: 0 var(--site-margin);
 margin: 0 auto;
}

.centered {
 text-align: center;
}

.flex-container {
 display: flex;
 align-items: center;
 justify-content: center;
}
@media (max-width: 750px) {
 .flex-container {
 flex-direction: column;
 }
}

section.row {
 padding-top: var(--content-row-padding);
 padding-bottom: var(--content-row-padding);
}

@media (min-width: 751px) {
 section.hero-row {
 padding-top: var(--site-margin);
 }
}
section.hero-row picture, section.hero-row img {
 width: 100%;
 max-width: 1202px;
}

section.gray-row {
 border-radius: 30px;
 box-sizing: border-box;
 width: calc(100% - var(--site-margin));
 margin: 0 auto;
 background-color: var(--gray-background-color);
 padding: var(--site-margin);
}

div#announcement-banner {
 text-align: center;
 padding: 8px 10px;
 font-size: 0.8em;
 color: var(--label-color);
 background-color: var(--announcement-bar-color);
}
div#announcement-banner a {
 font-weight: bold;
}

body > header {
 padding: 8px 0;
 font-size: 0.9em;
 position: sticky;
 top: 0;
 left: 0;
 right: 0;
 z-index: 99;
 transition: all 0.2s ease;
 border-bottom: 1px solid transparent;
}
@media (max-width: 750px) {
 body > header {
 padding: 15px 0;
 }
}
body > header.floating {
 backdrop-filter: blur(20px);
 -webkit-backdrop-filter: blur(20px);
 border-bottom: 1px solid var(--separator-color);
 background-color: rgba(255, 255, 255, 0.72);
}
@media (prefers-color-scheme: dark) {
 body > header.floating {
 background-color: rgba(30, 30, 30, 0.72);
 }
}
body > header > div {
 height: 100%;
 display: flex;
 align-items: center;
}
body > header .mobile-nav-toggle {
 display: none;
}
@media (max-width: 750px) {
 body > header .mobile-nav-toggle {
 display: inherit;
 flex: 0 1 10%;
 }
}
body > header .icon {
 width: 32px;
 height: 32px;
 vertical-align: bottom;
 filter: none;
}
@media (prefers-color-scheme: dark) {
 body > header .icon {
 filter: invert(100%);
 }
}

header nav {
 flex: 0 1 67%;
 text-align: right;
}
header nav a {
 text-decoration: none;
 color: rgb(70, 70, 70);
}
@media (prefers-color-scheme: dark) {
 header nav a {
 color: white;
 }
}
@media (min-width: 751px) {
 header nav a.selected, header nav a:hover {
 border-bottom: 2px solid var(--control-accent-color);
 padding-bottom: 10px;
 }
}
header nav a + a {
 margin-left: 18px;
}
header nav a.mobile-nav-toggle {
 margin-bottom: 40px;
}
header nav .download-button {
 margin-left: 1em;
 padding: 10px 20px;
}
header nav .download-button:hover {
 background-color: var(--system-blue-color);
 color: white;
 border-color: var(--system-blue-color);
}
@media (max-width: 750px) {
 header nav {
 text-align: left;
 position: absolute;
 top: 0;
 left: -205px;
 height: 100vh;
 width: 150px;
 font-size: 1.2em;
 border-right: 1px solid #999;
 padding: var(--site-margin);
 background-color: rgba(255, 255, 255, 0.72);
 backdrop-filter: blur(20px);
 -webkit-backdrop-filter: blur(20px);
 z-index: 999;
 transition: 0.2s ease;
 box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
 }
}
@media (max-width: 750px) and (prefers-color-scheme: dark) {
 header nav {
 border-right: #333;
 box-shadow: 0 0 5px black;
 background-color: rgba(30, 30, 30, 0.72);
 }
}
@media (max-width: 750px) {
 header nav a {
 display: block;
 margin: 0;
 }
 header nav a + a {
 margin-top: 20px;
 margin-left: 0;
 }
 header nav .download-button {
 display: none;
 }
}

body.sidebar-enabled nav {
 left: 0;
}

.logo-container {
 margin: auto 0;
 flex: 0 1 33%;
}
@media (max-width: 750px) {
 .logo-container {
 flex: 0 1 80%;
 text-align: center;
 }
}
.logo-container .app-icon {
 width: 48px;
 height: 48px;
 margin-left: -5px;
}
@media (max-width: 750px) {
 .logo-container .app-icon {
 display: none;
 }
}
.logo-container a {
 color: inherit;
 font-weight: inherit;
 display: flex;
 align-items: center;
 gap: 10px;
}
@media (max-width: 750px) {
 .logo-container a {
 justify-content: center;
 }
}

footer {
 max-width: 1200px;
 width: calc(100% - 2 * var(--site-margin));
 margin: 0 auto;
 padding: var(--site-margin) 0;
 border-top: 1px solid var(--separator-color);
 color: var(--label-color);
 font-size: 0.8em;
 display: flex;
 gap: 1em;
 justify-content: space-between;
 vertical-align: center;
}
@media (max-width: 750px) {
 footer {
 flex-direction: column-reverse;
 align-items: center;
 gap: calc(2 * var(--site-margin));
 }
}
@media (max-width: 750px) {
 footer > div {
 text-align: center;
 }
}
footer > div .logo {
 max-width: 175px;
}
footer > div .copyright {
 padding: 0.75em 0;
 line-height: 150%;
 color: var(--secondary-label-color);
 font-size: 0.9em;
}
footer > div .social-icons {
 margin-left: -4px;
 margin-top: 0.5em;
}
footer > div .social-icons img {
 height: 26px;
 opacity: 0.5;
}
@media (prefers-color-scheme: dark) {
 footer > div .social-icons img {
 filter: invert(100%);
 }
}
footer > div .social-icons a:hover img {
 opacity: 1;
}
footer > nav {
 display: flex;
 gap: 2em;
}
@media (max-width: 750px) {
 footer > nav {
 gap: 3em;
 flex-wrap: wrap;
 }
}
footer > nav > section {
 display: flex;
 flex-direction: column;
}
footer > nav > section h5 {
 font-size: 1em;
 margin-top: 0;
 margin-bottom: 1em;
 font-weight: bold;
}
footer > nav > section a {
 color: var(--text-color);
}
footer > nav > section a:hover {
 text-decoration: underline;
}
footer > nav > section a + a {
 margin-top: 1em;
}

.feature-card-grid {
 display: grid;
 grid-template-columns: 1fr 1fr 1fr;
 grid-gap: 24px;
}
@media (max-width: 750px) {
 .feature-card-grid {
 grid-template-columns: 1fr;
 }
}

@media (max-width: 1000px) and (min-width: 751px) {
 .feature-card-grid.tablet-layout {
 grid-template-columns: 1fr 1fr;
 }
 .feature-card-grid.tablet-layout .tablet-wide-card {
 order: -1;
 grid-column: 1/span 2;
 }
}
.feature-card {
 border-radius: 20px;
 border: 1px solid var(--separator-color);
 background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.4));
 box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.08);
 padding: 30px;
 overflow: hidden;
}
@media (prefers-color-scheme: dark) {
 .feature-card {
 background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1));
 background-color: rgb(34, 34, 34);
 box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
 }
}
.feature-card p {
 margin-bottom: 2em;
}
.feature-card.wide-start {
 grid-column: 1/span 2;
}
@media (max-width: 750px) {
 .feature-card.wide-start {
 grid-column: auto;
 }
}
.feature-card.wide-end {
 grid-column: 2/span 2;
}
@media (max-width: 750px) {
 .feature-card.wide-end {
 grid-column: auto;
 }
}
.feature-card.purple {
 background-color: var(--system-purple-color-light);
 border-color: var(--system-purple-color-medium);
}
.feature-card.purple h2 {
 color: var(--system-purple-color);
}
.feature-card.mint {
 background-color: var(--system-mint-color-light);
 border-color: var(--system-mint-color-medium);
}
.feature-card.mint h2 {
 color: var(--system-mint-color);
}
.feature-card.cyan {
 background-color: var(--system-cyan-color-light);
 border-color: var(--system-cyan-color-medium);
}
.feature-card.cyan h2 {
 color: var(--system-cyan-color);
}
.feature-card.blue {
 background-color: var(--system-blue-color-light);
 border-color: var(--system-blue-color-medium);
}
.feature-card.blue h2 {
 color: var(--system-blue-color);
}
.feature-card.orange {
 background-color: var(--system-orange-color-light);
 border-color: var(--system-orange-color-medium);
}
.feature-card.orange h2 {
 color: var(--system-orange-color);
}
.feature-card.green {
 background-color: var(--system-green-color-light);
 border-color: var(--system-green-color-medium);
}
.feature-card.green h2 {
 color: var(--system-green-color);
}
.feature-card.red {
 background-color: var(--system-red-color-light);
